          visible: false,
          url: 'https://gis.sinica.edu.tw/tileserver/file-exists.php?img=TM25K_1993-jpg-{z}-{x}-{y}',
          attribution:
            '中央研究院 WMTS 服務"',
        },
        {
          name: '2001臺灣經建3版地形圖',
          visible: false,
          url: 'https://gis.sinica.edu.tw/tileserver/file-exists.php?img=TM25K_2001-jpg-{z}-{x}-{y}',
          attribution:
            '中央研究院 WMTS 服務"',
        },
        {
          name: '2003臺灣經建4版地形圖',
          visible: false,
          url: 'https://gis.sinica.edu.tw/tileserver/file-exists.php?img=TM25K_2003-jpg-{z}-{x}-{y}',
          attribution:
            '中央研究院 WMTS 服務"',
        },
        {
          name: '1953竹北二堡上北勢庄地籍圖',
          visible: false,
          url: 'https://gis.sinica.edu.tw/dijitu/file-exists.php?img=tj7a0421-png-{z}-{x}-{y}',
          attribution:
            '中央研究院 WMTS 服務"',
        }
      ],
      
    }
  },
  methods: {
    sent: function(place) {
      parent.postMessage(place.sys_id, "*")
    },
    drawMakers: function(json) {
            //console.log( json );
            if (this.markers !== null) {
                this.$refs.myMap.mapObject.removeLayer(this.markers)
            }
            this.markers = L.markerClusterGroup()
            let str = ''
            json.filter(x => x['xy coordinates'].length > 0).forEach((value) => {
                //alert( key + ": " + value );
                // console.log(value)
                const id = value.sys_id
                const name = value.name
                const parentName = value['parent name']
                const years = value.years
                const [lng,lat] = value['xy coordinates'].split(',')
                str = str + '<br><span id="' + id + '"><a class="hook">' + id + '</a><br>' + name + ' ' + parentName + '<br><span class="">(' + years + ') [' + value['xy coordinates'] + ']</span></span><br>\n'
    
                const marker = L.marker(new L.LatLng(lat, lng), { title: name })
                marker.bindPopup(`${id}<br/>${name}<br/>${parentName}<br/>${years}`)
                this.markers.addLayer(marker)
                // this.places.push(value)
            })
            // console.log(json)
            this.places = json
            // console.log(this.places)
            // this.list = str
            this.$refs.myMap.mapObject.addLayer(this.markers)
            this.$refs.myMap.mapObject._resetView(this.$refs.myMap.mapObject.getCenter(), this.$refs.myMap.mapObject.getZoom(), true)
            // this.$refs.myMap.mapObject.eachLayer(function(layer){
            //     console.log(layer) 
            // })
        },
    onMapClick: function(e) {
      this.$refs.myMap.mapObject._resetView(this.$refs.myMap.mapObject.getCenter(), this.$refs.myMap.mapObject.getZoom(), true)
      // this.$refs.myMap.mapObject.eachLayer(function(layer){
      //   console.log(layer)
      //     // layer.redraw()
      // })
      this.popup.setLatLng(e.latlng)
                    .setContent(e.latlng.toString()).openOn( this.$refs.myMap.mapObject)
    },
    text2Number: function (str) {
      return str.replace('番', '').replace('│', '-').replace('|', '-').replace('ノ', '-').replace('之', '-').replace(/○/g, '0').replace(/一/g, '1').replace(/二/g, '2').replace(/三/g, '3').replace(/四/g, '4').replace(/五/g, '5').replace(/六/g, '6').replace(/七/g, '7').replace(/八/g, '8').replace(/九/g, '9').replace(/十/g, '0')
    },
    dealCadastre: function (cadaNoStr) {
      let isCadastre = false
      let tmp1 = ''
      let tmp2 = ''
      let obj = {'isCadastre': false }

      const regex = /[一二三四五六七八九十○]{1,3}(│|\||ノ|番?之)[一二三四五六七八九十○]{1,3}/g
      let m;
      while ((m = regex.exec(cadaNoStr)) !== null) {
          // This is necessary to avoid infinite loops with zero-width matches
          if (m.index === regex.lastIndex) {
              regex.lastIndex++
          }
          tmp1 = m[0]
          tmp2 = this.text2Number(m[0])
          isCadastre = true
      }

      if (!isCadastre) {
        obj.isCadastre = false
        return obj
      }

      cadaNoStr = cadaNoStr.replace(tmp1, tmp2).replace(/\s/g, '')

      if (cadaNoStr.indexOf("土名") > -1) {
        const regex = /([\u4e00-\u9fa5]+堡)([\u4e00-\u9fa5]+庄)土名([\u4e00-\u9fa5]{2,4})(\d+)-(\d+)/g
        let m
        while ((m = regex.exec(cadaNoStr)) !== null) {
          // This is necessary to avoid infinite loops with zero-width matches
          if (m.index === regex.lastIndex) {
              regex.lastIndex++
          }
          //console.log(m[0]);
          obj.isCadastre = true
          obj.b = m[1]
          obj.z = m[2]
          obj.d = m[3]
          obj.mn = m[4]
          obj.sn = m[5]
        }
      } else {
        const regex = /([\u4e00-\u9fa5]+堡)([\u4e00-\u9fa5]+庄)(\d+)-(\d+)/g
        let m
        while ((m = regex.exec(cadaNoStr)) !== null) {
          // This is necessary to avoid infinite loops with zero-width matches
          if (m.index === regex.lastIndex) {
              regex.lastIndex++
          }
          //console.log(m[0]);
          obj.isCadastre = true
          obj.b = m[1]
          obj.z = m[2]
          obj.d = ''
          obj.mn = m[3]
          obj.sn = m[4]
        }
      }
      return obj
    }
  },
   created : async function () {
      let response = await fetch('https://dhumanities.github.io/markus/js/kPlaceLatLon.json')
      // console.log()
      let KP = await response.json()
      // console.log(KP)
      const queryString = window.location.search;
      // console.log(window.location)
      const urlParams = new URLSearchParams(queryString)
      const n = urlParams.get('n')
      // console.log(this.$route.query)
      const cada = this.dealCadastre(n)
      // console.log(cada)

      if (cada.isCadastre) {

          //console.log(cada);
          const url = 'https://docusky.org.tw/DocuSky/docutools/geocode/twcada.php?t=&b=' + encodeURIComponent(cada.b) + '&z=' + encodeURIComponent(cada.z) + '&d=' + encodeURIComponent(cada.d) + '&n=' + cada.mn
          const response = await fetch(url);
          const json = await response.json()
          if (json.placenames.length > 0) {
            this.drawMakers(json.placenames)
          } else {
                  // $('#list').html('<h2>無資料 / None<h2>')
          }
          return
      }

      if (n !== null || n.length > 0) {
          //console.log(n.indexOf("twcada_"));
          if (n.indexOf("twcada_") > -1) {
              const url = "https://docusky.org.tw/DocuSky/docutools/geocode/twcada.php?n=" + n
              const response = await fetch(url)
              const json = await response.json()
              if (json.placenames.length > 0) {
                // console.log(json.placenames)
                this.drawMakers(json.placenames)
              } else {
                  // $('#list').html('<h2>無資料 / None<h2>')
              }

          } else {
              let allJson = []
              const url = 'https://docusky.org.tw/DocuSky/docutools/geocode/tw.php?n=' + encodeURIComponent(n)
              let response = await fetch(url)
              let json = await response.json() || {placenames: []}
              // console.log(json)
              if (json.placenames.length > 0) {
                  allJson = allJson.concat(json.placenames)
              }
              // let cnurl = 'https://maps.cga.harvard.edu/tgaz/placename?fmt=json&n=' + (n)
              const cnurl = 'https://dh.chinese-empires.eu/chgis?fmt=json&n=' + (n)
              try {
                response = await fetch(cnurl)
                json = await response.json() || {placenames: []}
                if (json.placenames.length > 0) {
                    allJson = allJson.concat(json.placenames)
                }
              } catch (e) {
                console.log(e)
              }
              const KPlaces = decodeURIComponent(n).split("|")

              const KPlaceNames = KP.filter(x => {

                  return KPlaces.reduce((acc, y) => {
                      // console.log(y, x[2], x[0], x[2].indexOf(y) > -1, ("koreanPlace_" + x[0]) === y)
                      return acc ? acc : acc ||
                      x[2].indexOf(y) > -1 || ("koreanPlace_" + x[0]) === y
                  }, false)
              }).sort((a, b)=> a[2].length > b[2].length ? 1 : a[2].length === b[1].length ? 0 : -1  ) || []

              allJson = KPlaceNames.reduce((acc, x)=> {
                  acc.push(
                              {
                                  "sys_id": "koreanPlace_" + x[0],
                                  // "url": "http://docusky.org.tw/DocuSky/Docutools/Geocode/map.html?n=twgis_1600",
                                  "name": `${x[2]}${x[3] ? `(${x[3]})` : ''}`,
                                  "transcription": "",
                                  "years": x[4]|| '',
                                  "parent sys_id": "",
                                  "parent name": "",
                                  "feature type": "POINT",
                                  "xy coordinates": x[6].length > 0 ? `${x[6]},${x[7]}` : '',
                                  "data source": ""
                              }
                          )
                  return acc

              }, allJson)
              this.drawMakers(allJson)
          }
      }
  }
}
</script>
